import React from 'react';
import {Row, Col,Card,Icon,Button,Pagination} from 'antd';
import styles from '../MainCases/MainCases.less';


function ArticlesList(props) {
  const {leftlist,rightlist,pagination, onPageChange,url,list,loadMore} = props;

  return (
    <div className={styles.normalArticles}>
      <div className={styles.lawyerImg}>
        <img src={url} alt=""width='194px' height='194px'/>
      </div>
      <div className={styles.articlesDiv}>我的博客</div>
     <div className={styles.articlesProfile}>来法仆，寻求法律帮助来法仆，寻求法律帮助来法仆，寻求法律帮助来法仆，寻求法律帮助</div>
      {list&&list.length>0?
      <div className={styles.articlesListContent}>
        <div style={{width:'100%',margin:'auto',textAlign:'center'}}><img  src="http://oonavif60.bkt.clouddn.com/default/articles/articlesList1.png" alt=""/></div>
        <Row gutter={16}>
        <div className={styles.articlesListLeft}>
          {leftlist && leftlist.length > 0? leftlist.map((data,index) =>{
            const {id, articleTitle, articleCover, author, articleContent, createdAt} = data;
            return(
              <a href={'/article/'+id} key={index} style={{color: 'gray', textDecorator: ''}}>
                <Col  lg={24} span={24} >
                  <Card bordered={false}>

                    <img src={articleCover}  width='433px' height='303px'/>
                    <div className={styles.productName}>
                      <p className={styles.casesTitle}>{articleTitle}</p>
                      <div className={styles.casescecontent} dangerouslySetInnerHTML={{__html: articleContent}}/>
                      <div style={{marginTop:'6px',color:'#0496ff'}}>
                        {createdAt}
                      </div>
                      <img src="http://oonavif60.bkt.clouddn.com/default/articles/articlesList2.png" alt=""/>
                    </div>

                  </Card>
                </Col>
              </a>
            )
          }) : ''}
          </div>
        <div className={styles.Dividingline}></div>
        <div className={styles.articlesListRight}>
          {rightlist && rightlist.length > 0? rightlist.map((data,index) =>{
            const {id, articleTitle, articleCover, author, articleContent, createdAt} = data;
            return(
                <a key={index}  href={'/article/'+id} style={{color: 'gray', textDecorator: ''}}>
                  <Col  lg={24} span={24} >
                  <Card bordered={false}>
                    <img src={articleCover}  width='433px' height='303px'/>
                    <div className={styles.productName}>
                      <p className={styles.casesTitle}>{articleTitle}</p>
                      <div className={styles.casescecontent} dangerouslySetInnerHTML={{__html: articleContent}}/>
                      <div style={{marginTop:'6px',color:'#0496ff'}}>
                        {createdAt}
                      </div>
                      <img src="http://oonavif60.bkt.clouddn.com/default/articles/articlesList2.png" alt=""/>
                    </div>
                  </Card>
                    </Col>
                </a>
            )
          }) : ''}
        </div>
        </Row>
      {/*<Row gutter={16}>*/}
        {/*{list && list.length > 0? list.map((data,index) =>{*/}
          {/*const {id, articleTitle, articleCover, author, articleContent, createdAt} = data;*/}
          {/*return(*/}
            {/*<Col  lg={8} span={12} style={{height: '330px'}}>*/}
              {/*<a  href={'/article/'+id} style={{color: 'gray', textDecorator: ''}}>*/}
                {/*<Card bordered={false}>*/}
                  {/*<img src={articleCover}  width='268px' height='178px'/>*/}
                  {/*<div className={styles.productName}>*/}
                    {/*<p className={styles.casesTitle}>{articleTitle}</p>*/}
                    {/*<div className={styles.casescecontent} dangerouslySetInnerHTML={{__html: articleContent}}/>*/}
                    {/*<div style={{marginTop:'6px',color:'#0496ff'}}>*/}
                      {/*{createdAt}*/}
                    {/*</div>*/}
                  {/*</div>*/}
                {/*</Card>*/}
              {/*</a>*/}
            {/*</Col>*/}
          {/*)*/}
        {/*}) : ''}*/}
      {/*</Row>*/}

      <Row className={styles.footerpage}>
        {pagination && pagination.total > 1 ?
          <div className={styles.MoreArticlesList} onClick={loadMore}>加载更多</div>
          :''}
      </Row>
      </div>
      :''}
    </div>
  );
}

export default ArticlesList;



